@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  overflow-x: hidden;
  overflow-x: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F9F8FD;
}

#root{
  display: flex;
  width: 100vw;
  height:100vh;
  justify-content: center;
  align-items: center;
  overflow-y: hidden;
  overflow-x: hidden;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; 
  
}

::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar space */
 /*background: transparent;   Optional: just make scrollbar invisible */
}

.mainContainer{
  background-color: #23262D;
  height: 80%;
  width: 55%;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}

.Header{
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  align-items: flex-start;
}

.Header hr{
  width: 100%;
  margin-top: -10px;
}

.ToDoBox{
  margin-top: 20px;
  height: 65%;
  padding: 2px 10px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.AddContainer{
  margin-top: 5px;
  height: 60px;
  display: flex;
  padding: 2px 10px;
  justify-content: space-between;
  align-items: center;
}


.AddContainer button{
  height: 40px;
  width: 40px;
  font-size: 20px;
  font-weight: bold;
  color: #FFF;
  background-color: #9481DD;
  border:none;
  border-radius: 50%;
  cursor: pointer;
}


.AddContainer input{
  width: 70%;
  height: 40px;
  border-radius: 15px;
  background-color: transparent;
  border: 1px solid #9481DD;
  outline: none;
  cursor: pointer;
  padding:2px 4px ;
  color: #FFF;
  font-weight: bold;
}

.ToDo{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 2px;
  border: 1px solid #9481DD;
  border-radius: 10px;
  padding: 2px 10px; 
}

.ToDo .options{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}

.ToDo .options button{
  margin-left: 10px;
  background-color: transparent;
  color: #BF1B31;
  border:none;
  cursor: pointer;
}

@media screen and (min-width:270px) and (max-width:750px) {
  #root{
    height: 92vh; 
    background-color: #23262D;

  }
  body{
    height: 92vh;
    background-color: #23262D;
  }
  .mainContainer{
    min-width: 100%;
    min-height: 100%;
    padding: 20px;
    justify-content: space-between;
  }
  .AddContainer input{
    width: 80%;
    font-size: 12px;
  }
}
